.container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.7rem;
  color: var(--color-text);
  background-color: var(--color-background);
  padding: 0.3rem 0.8rem;
  border-radius: 1rem;
  width: max-content;
  -webkit-user-select: none;
  user-select: none;
}